import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { environment } from "../../../environments/environment";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";

@Injectable({ providedIn: "root" })
export class LeaveService {
    access_token = localStorage.getItem("access_token");
    headers = new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.access_token,
    });
    options = { headers: this.headers };
    leaves = new Subject<any>();
    leave = new Subject<any>();

    constructor(private http: HttpClient) {}

    public getLeavesData() {
        return this.leaves.asObservable();
    }

    public getleaveData() {
        return this.leave.asObservable();
    }

    get() {
        return this.http
            .get<any>(`${environment.apiUrl}/api/leave`, this.options)
            .pipe(
                map((leaves) => {
                    this.leaves.next(leaves);
                    return leaves;
                })
            );
    }

    findById(leaveId: any) {
        return this.http
            .get<any>(
                `${environment.apiUrl}/api/leave/${leaveId}`,
                this.options
            )
            .pipe(
                map((leave) => {
                    this.leave.next(leave);
                    return leave;
                })
            );
    }

    delete(leaveId: any) {
        return this.http
            .delete<any>(
                `${environment.apiUrl}/api/leave/${leaveId}`,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    add(data: any) {
        const date = `${data.date.year}-${data.date.month}-${data.date.day}`;
        const body = {
            employee: data.employee,
            date: date,
            duration: data.duration,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/leave/create`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    update(data: any, leaveId: any) {
        const date = `${data.date.year}-${data.date.month}-${data.date.day}`;
        const body = {
            employee: data.employee,
            date: date,
            duration: data.duration,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/leave/update/${leaveId}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }
}
