import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../router.animations";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { AddComponent } from "./add/add.component";
import { EditComponent } from "./update/edit.component";
import { LeaveService } from "src/app/shared/services/leave.service";

@Component({
    selector: "app-leave",
    templateUrl: "./leave.component.html",
    styleUrls: ["./leave.component.scss"],
    animations: [routerTransition()]
})
export class LeaveComponent implements OnInit {
    leaves: any;
    constructor(
        private dialogService: NgbModal,
        private leaveService: LeaveService
    ) {}

    ngOnInit() {
        this.leaveService.get().subscribe(() => {});
        this.leaveService.getLeavesData().subscribe(data => {
            this.leaves = data.leaves;
        });
    }

    onAdd() {
        this.dialogService.open(AddComponent, {
            size: "lg",
            centered: true
        });
    }

    onUpdateClick(leaveId: any) {
        const modalRef = this.dialogService.open(EditComponent, {
            size: "lg",
            centered: true
        });
        modalRef.componentInstance.leaveId = leaveId;
    }

    onDeleteClick(leaveId: any) {
        this.leaveService.delete(leaveId).subscribe(data => {
            this.leaveService.get().subscribe(() => {});
        });
    }
}
